<template>
    <div>
        <div class="text-right mt-5">
            <!--  -->            
            <button class="btn btn-primary-light" @click="openModal(true)"></button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th width=120></th>
                    <th width=120></th>
                    <th width=120></th>
                    <th width=120></th>
                    <th width=80></th>
                    <th width=80></th>
                    <th width=80></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td>{{item.category}}</td>
                    <td>{{item.title}}</td>
                    <td>{{item.origin_price}}</td>
                    <td>{{item.price}}</td>
                    <td>
                        <span v-if="item.is_enabled"></span>
                        <span v-else></span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)"></button>
                    </td>
                    <td><button class="btn btn-outline-primary btn-sm" @click="delModal(item)"></button ></td>
                </tr>
            </tbody>
            
        </table>
        <!--  -->
        <pagination :page="pagination" @pagechange="getProduct"/>
        <div class="modal fade" id="openModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" aria-label="Close" data-dismiss="modal">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                    <div class="col-sm-4">
                        <div class="form-group">
                        <label for="image"></label>
                        <input type="text" class="form-control" id="image"
                            placeholder="" v-model="tempProduct.image">
                        </div>
                        <div class="form-group">
                        <label for="customFile"> 
                            <!-- <font-awesome-icon icon="spinner" class="fas fa-backward fa-spin" v-if="status.filesLoading"/> -->
                            <!-- <i class="fas fa-spinner fa-spin" v-if="status.filesLoading"></i> -->
                        </label>
                        <input type="file" id="customFile" class="form-control"
                            ref="files">
                        </div>
                        <img 
                        class="img-fluid" alt="" :src="tempProduct.image">
                    </div>
                    <div class="col-sm-8">
                        <div class="form-group">
                        <label for="title"></label>
                        <input type="text" class="form-control" id="title"
                            placeholder="" v-model="tempProduct.title">
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                            <label for="category"></label>
                            <input type="text" class="form-control" id="category"
                            placeholder="" v-model="tempProduct.category">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price"></label>
                            <input type="unit" class="form-control" id="unit"
                            placeholder="" v-model="tempProduct.unit">
                        </div>
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                        <label for="origin_price"></label>
                            <input type="number" class="form-control" id="origin_price"
                            placeholder="" v-model="tempProduct.origin_price">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price"></label>
                            <input type="number" class="form-control" id="price"
                            placeholder="" v-model="tempProduct.price">
                        </div>
                        </div>
                        <hr>

                        <div class="form-group">
                        <label for="description"></label>
                        <textarea type="text" class="form-control" id="description"
                            placeholder="" v-model="tempProduct.description"></textarea>
                        </div>
                        <div class="form-group">
                        <label for="content"></label>
                        <textarea type="text" class="form-control" id="content"
                            placeholder="" v-model="tempProduct.content"></textarea>
                        </div>
                        <div class="form-group">
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox"
                            id="is_enabled"
                            :true-value="1" :false-value="0">
                            <label class="form-check-label" for="is_enabled">
                            
                            </label>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary" @click="updateProduct"></button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Pagination from '../../components/Pagination'
import $ from 'jquery'
// import pagination from '../../components/Pagination'

export default {
    
    data(){
        return{
            // 
            products:[],
            // 
            tempProduct:{
            },
            // 
            isNew:false,
            // JSON
            isLoading:false,
            // 
            status:{
                filesLoading: false,
            },
            // 
            pagination:{},
        }
    },
    components:{
        Pagination,
    },
    methods:{
        //  (created))
        getProduct(page = 1){
            const vm = this
            vm.isLoading = true
            const API = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USERPATH}/admin/products?page=${page}`
            this.axios.get(API).then((res)=>{
                vm.products = res.data.products
                console.log(res)
                vm.isLoading = false
                // 
                vm.pagination = res.data.pagination
            })
        },
//         // modal ())
        openModal(isNew,item){
            $('.modal').modal('show')
            console.log(isNew)
            if(isNew){
                this.tempProduct = {}
                this.isNew = true
            }else{
                this.tempProduct = Object.assign({},item)
                this.isNew = false
            }
        },
        modalClose(){
            $('.modal').hide()
        },
        updateProduct(){
            const vm = this
            let API = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USERPATH}/admin/product`
            let httpMethod = 'post'
// 
// ISNEWAPI
            if(!vm.isNew){
                API = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USERPATH}/admin/product/${vm.tempProduct.id}`
                httpMethod = 'put'
            }
            this.axios[httpMethod](API,{data:vm.tempProduct}).then((res)=>{
                if(res.data.success){
                    $('#openModal').modal('hide')
                    vm.getProduct()
                }else{
                    $('#openModal').modal('hide')
                    vm.getProduct()
                    alert('error')
                }
            })  
        },
        delModal(item){
            const vm = this
            vm.tempProduct = item
            const API = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USERPATH}/admin/product/${item.id}`
            this.axios.delete(API).then((res)=>{
                vm.products = res.data.products
                vm.getProduct()
            })  
        },
        uploadFile(){
            const vm = this
            const uploadFiles = vm.$refs.files.files[0]
            const API = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USERPATH}/admin/upload`
            const formData = new FormData()
            vm.status.filesLoading = true
            formData.append('file',uploadFiles)
            vm.axios.post(API,formData,{
                header:{
                    'Content-Type': 'multipart/form-data'
                }
            }).then((res)=>{
                // vm.tempProduct.img = res.data.imageUrl
                if(res.data.success){
                    vm.status.filesLoading = false
                    vm.$set(vm.tempProduct,'img',res.data.imageUrl)
                }else{
                    // this.$bbb.$emit('message:push',res.data.message,'danger')
                }
            })
        }
    },
    created(){
        this.getProduct()
    }
}
</script>