<template>
    <div>
        <div class="container pc d-none d-sm-none d-md-block position-relative">
            <div class="row">
                <ul class="list col-12 pb-3">
                    <li :class="{'active':pageone}"></li>
                    <li :class="{'active':pagetwo}"></li>
                    <li ></li>
                </ul>
            </div>
            <div class="row page-transition position-absolute" :class="{'hild-left':!pageone}">
                <div class="col-7" v-if="cartbag.carts.length == 0 "></div>
                <div class="col-7" v-if="cartbag.carts.length !== 0 ">
                    <table class="table">
                        <thead class="bg-primary text-white">
                            <th width=100></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </thead>
                        <tbody class="bg-primary-light" v-for="item in cartbag.carts" :key="item.product.id">
                        <td><img :src="item.product.image" class="img-fluid" alt=""></td>
                        <td>{{item.product.title}}
                            <div v-if="item.coupon" class="text-secondary-dark">
                                
                            </div>
                        </td> 
                        <td>{{item.qty}} / pic</td>
                        <td>{{item.product.price | currency}}
                        </td>
                        </tbody>
                        <tfoot>
                        </tfoot>
                    </table>
                </div>
                <div class="col-5 ">
                    <table class="table">
                        <thead>
                            <th colspan="2"></th>
                        </thead>
                        <tbody>
                            <td v-if="cartbag.final_total == cartbag.total">{{cartbag.total | currency}} </td>
                            <td v-if="cartbag.final_total !== cartbag.total" class="text-danger"><del>{{cartbag.total | currency}}</del>
                            <div v-if="cartbag.carts.coupon"></div>
                            </td>
                            <td v-if="cartbag.final_total !== cartbag.total">{{cartbag.final_total | currency}} </td>
                            
                        </tbody>
                        <tfoot>
                            <td colspan="4">
                                <div class="input-group mb-3">
                                    <input type="text" class="form-control" placeholder="" aria-label="Recipient's username" aria-describedby="button-addon2" v-model="coupon_code">
                                    <div class="input-group-append">
                                        <button class="btn btn-outline-secondary" type="button" id="button-addon2" @click="addCouponCode"></button>
                                    </div>
                                </div>
                            </td>
                        </tfoot>
                    </table>
                    <table class="table">
                        <thead>
                            <td v-if="cartbag.carts.length !== 0">
                                <button type="button" class="btn btn-primary-dark w-100" @click="pageone = !pageone,pagetwo = !pagetwo"></button>
                            </td>
                            <td v-if="cartbag.carts.length == 0">
                                <button type="button" class="btn btn-primary-dark w-100" @click="pageone = !pageone,pagetwo = !pagetwo" disabled></button>
                            </td>
                        </thead>
                    </table>
                </div>
            </div>
            <!-- 2 -->
            <ValidationObserver ref="observer"  v-slot="{ invalid }" tag="form" >
                <form class="row page-transition position-absolute" :class="{'hild-left':!pagetwo}" @submit.prevent="sendOrder">
                <div class="form-group col-6">
                    <label for="useremail">Email *</label>
                    <ValidationProvider name="email" rules="required|email" v-slot="{ errors }">
                    <input
                        type="email"
                        class="form-control"
                        name="email"
                        id="useremail"
                        v-model="form.user.email"
                        placeholder=" Email"
                        required
                    />
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                </div>
                <div class="form-group col-6">
                    <label for="username"> *</label>
                    <ValidationProvider v-slot="{ errors }" rules="required" name="">
                    <input
                        type="text"
                        class="form-control"
                        name="name"
                        id="username"
                        v-model="form.user.name"
                        placeholder=""
                    />
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                </div>

                <div class="form-group col-6">
                    <label for="usertel"> *</label>
                    <ValidationProvider v-slot="{ errors }" rules="required" name="">
                    <input
                        type="tel"
                        class="form-control"
                        id="usertel"
                        v-model="form.user.tel"
                        placeholder=""
                    />
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                </div>

                <div class="form-group col-6">
                    <label for="useraddress"> *</label>
                    <ValidationProvider v-slot="{ errors }" rules="required" name="">
                    <input
                        type="text"
                        class="form-control"
                        name="address"
                        id="useraddress"
                        v-model="form.user.address"
                        placeholder=""
                    />
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                </div>

                <div class="form-group col-12">
                    <label for="comment"></label>
                    <textarea
                    name
                    id="comment"
                    class="form-control"
                    cols="30"
                    rows="10"
                    v-model="form.message"
                    ></textarea>
                </div>
                <div class="col-6">
                    <button class="btn btn-primary-dark w-100" @click="pageone = !pageone,pagetwo = !pagetwo" type="button"></button>
                </div>
                <div class="col-6 text-right">
                    <button class="btn btn-danger" :disabled="invalid"></button>
                </div>
                </form>
            </ValidationObserver>
            <!-- 3  -->
        </div>
        <!--  -->
        <div class="container mobile d-sm-block d-md-none">
            <div class="row " v-for="item in cartbag.carts" :key="item.product.id">
                <div class="col-6">
                    <img :src="item.product.image" alt="" class="img-fluid">
                </div>
                <div class="col-5">
                    <p>{{item.product.title}} 
                    </p>
                    <p>{{item.product.price}} * {{item.qty}} {{item.product.unit}}</p>
                    <p>{{item.final_total | currency}} <span v-if="item.coupon" class="text-secondary-dark"></span></p>
                </div>
                <div class="col-1">
                    <button class="btn p-0" @click="removeCart(item.id)">
                        <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="trash" class="svg-inline--fa fa-trash fa-w-14 text-danger" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path fill="#FF6464" d="M432 32H312l-9.4-18.7A24 24 0 0 0 281.1 0H166.8a23.72 23.72 0 0 0-21.4 13.3L136 32H16A16 16 0 0 0 0 48v32a16 16 0 0 0 16 16h416a16 16 0 0 0 16-16V48a16 16 0 0 0-16-16zM53.2 467a48 48 0 0 0 47.9 45h245.8a48 48 0 0 0 47.9-45L416 128H32z"></path></svg>
                    </button>
                </div>
            </div>
            <div class="row">
                <div class="col-12">
                    <div class="input-group mb-3">
                        <input type="text" class="form-control" placeholder="" aria-label="Recipient's username" aria-describedby="button-addon2" v-model="coupon_code">
                        <div class="input-group-append">
                            <button class="btn btn-outline-secondary" type="button" id="button-addon2" @click="addCouponCode"></button>
                        </div>
                    </div>
                </div>
                <div class="col-6">
                    
                </div>
                <div class="col-6">
                    <p>{{cartbag.final_total | currency}} </p>
                </div>
            </div>
            <ValidationObserver ref="observer"  v-slot="{ invalid }" tag="form" >
                <form class="row" @submit.prevent="sendOrder">
                <div class="form-group col-6">
                    <label for="m_useremail">Email *</label>
                    <ValidationProvider name="email" rules="required|email" v-slot="{ errors }">
                    <input
                        type="email"
                        class="form-control"
                        name="email"
                        id="m_useremail"
                        v-model="form.user.email"
                        placeholder=" Email"
                        required
                    />
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                </div>
                <div class="form-group col-6">
                    <label for="m_username"> *</label>
                    <ValidationProvider v-slot="{ errors }" rules="required" name="">
                    <input
                        type="text"
                        class="form-control"
                        name="name"
                        id="m_username"
                        v-model="form.user.name"
                        placeholder=""
                    />
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                </div>

                <div class="form-group col-6">
                    <label for="m_usertel"> *</label>
                    <ValidationProvider v-slot="{ errors }" rules="required" name="">
                    <input
                        type="tel"
                        class="form-control"
                        id="m_usertel"
                        v-model="form.user.tel"
                        placeholder=""
                    />
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                </div>

                <div class="form-group col-6">
                    <label for="m_useraddress"> *</label>
                    <ValidationProvider v-slot="{ errors }" rules="required" name="">
                    <input
                        type="text"
                        class="form-control"
                        name="address"
                        id="m_useraddress"
                        v-model="form.user.address"
                        placeholder=""
                    />
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                </div>

                <div class="form-group col-12">
                    <label for="m_comment"></label>
                    <textarea
                    name
                    id="m_comment"
                    class="form-control"
                    cols="30"
                    rows="10"
                    v-model="form.message"
                    ></textarea>
                </div>
                <div class="col-12 text-right">
                    <button class="btn btn-danger" :disabled="invalid"></button>
                </div>
                </form>
            </ValidationObserver>
        </div>
        </div>
</template>

<style lang="scss" scoped>
.hild-left{
    opacity: 0;
    transform: translateX(-150%);
}
.page-transition{
    transition: all 0.3s;
}

</style>

<script>
export default {
    data(){
        return{
            cartbag:[],
            pageone:true,
            pagetwo:false,
            pagethree:false,
            coupon_code:'',
            form:{
                user:{
                    name:'',
                    email:'',
                    tel:'',
                    address:''
                },
                message:'',
            },
            isLoading:false,

        }
    },
    computed:{
    },
    methods:{
        removeCart(id){
            const vm = this
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USERPATH}/cart/${id}`
            vm.axios.delete(url).then((res)=>{
                if(res.data.success){
                    vm.getCart()
                }
            })
        },
        getCart(){
            const vm = this
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USERPATH}/cart`
            vm.axios.get(url).then(res=>{
                vm.cartbag = res.data.data
            })
        },
        addCouponCode(){
            const vm = this
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USERPATH}/coupon`
            const coupon = {
                code : vm.coupon_code
            }
            vm.isLoading = true
            vm.axios.post(url,{data:coupon}).then(res=>{
                if(res.data.success){
                    vm.$bus.$emit('message:push',res.data.message,'success');
                    vm.getCart()
                    vm.isLoading = false
                }else{
                    vm.$bus.$emit('message:push',res.data.message,'wraning');
                }
            })
        },
        sendOrder(){
            const vm = this
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USERPATH}/order`
            console.log('click')
            let form = vm.form
            vm.axios.post(url,{data:form}).then(res=>{
                if(res.data.success){
                    vm.$router.push(`customorder/${res.data.orderId}`)
                }
            })
        }
    },
    created(){
        this.getCart()
    }
}
</script>